import React, { Component } from "react";
import { Link } from "react-router-dom";

const menu = [
  {
    link: "/",
    label: ""
  },
  {
    link: "/delivery",
    label: "  "
  },
  {
    link: "/ourworks",
    label: " "
  },
  {
    link: "/franchise",
    label: ""
  },
  {
    link: "/contacts",
    label: ""
  },
  {
    link: "/about",
    label: " "
  }
];

class Menu extends Component {
    render() {
        return (
            <div className="menu">
                {menu.map((item, index) =>
                    <div className="menu__item" key={index} >
                        <Link to={item.link} className="menu__link">
                            {item.label}
                            </Link>
                    </div>
                )}
            </div>
        );
    }
}

export default Menu;



