import React, { Component } from "react";
import { Link } from "react-router-dom";

const menu = [
  {
    link: "/shop/tulips",
    label: ""
  },
  {
    link: "/shop/roses",
    label: ""
  },
  {
    link: "/shop/bouquet",
    label: ""
  },  
  {
    link: "/shop/compositions",
    label: ""
  },
  {
    link: "/shop/wedding",
    label: ""
  },
  {
    link: "/shop/indoor",
    label: ""
  },
  {
    link: "/shop/sweets",
    label: ""
  }
];

class Range extends Component {
    render() {
        return (
            <div className="range">
                <div className="range__wrapper">
                    {menu.map((item, index) =>
                        <div className="range__item" key={index}>
                            <Link to={item.link} className="range__link">
                                {item.label}
                            </Link>
                        </div>
                    )}
                </div>
            </div>
        );
    }
}

export default Range;



